@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";

// 產險商品 3 x 2
.cathay-carosel{
  &.#{$prefix}-layout-02{
    
    padding-top: 25px;

    @media (min-width: map-get($grid-breakpoints, 'md')){
      padding-top: 0px;
      // padding-bottom: 0;
    }

    .#{$prefix}slider{
      display: flex;
      flex-wrap: wrap;

      >div,>a{
        padding:0 2%;
        flex: 1 1 120px;
        &:nth-child(n+4){
          margin-top: 25px;
        }
      }
      &.cs-length-4{
        >div,>a{
          /* 3 x 2 */
          flex: 1 1 150px;
          &:nth-child(n+3){
            margin-top: 25px;
          }
        }
      }
    }

    .#{$prefix}title{
      font-size:18px;
      font-weight: 400;
      line-height: 1;
      color:#333333;
      letter-spacing: 2px;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:20px;
        letter-spacing: 3px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }
    .#{$prefix}content{
      font-size:18px;
      line-height: 1.5;
      color:#999999;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:20px;
        letter-spacing: 3px;
      }
      .#{$prefix}price{
        color:#f9556f
      }
    }
    .#{$prefix}txt-wrap{
      display: block;
      padding:0px;
      position: relative;
      margin-top: 10px;
      text-align:center;
      width: 100%;
      @media (min-width: map-get($grid-breakpoints, 'md')){
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }
    .#{$prefix}title + .#{$prefix}content{
      margin-top: 10px;
    }

    .#{$prefix}img-wrap{
      background-size: cover;
      overflow: hidden;
      background-repeat: no-repeat;
      background-position: center top;
      margin: 0 auto;
      vertical-align: middle;

      img{
        visibility: hidden;
      }

      $ratio:.18;
      width: round($ratio * 270px);
      height: round($ratio * 246px);
      
      @media (min-width: map-get($grid-breakpoints, 'md')){
        $ratio:.3;
        width: round($ratio * 270px);
        height: round($ratio * 246px);
      }
    }
    .slick-slide {
        border-radius: 5px;
    }
    &.#{$prefix}-gutter{
     .slick-slide {
        margin: 0 12px;
        padding:20px 0px;
        box-shadow: 1px 0px 5px 1px rgba(0,0,0,0);
        -webkit-box-shadow: 1px 0px 5px 1px rgba(0,0,0,0);
        -moz-box-shadow: 1px 0px 5px 1px rgba(0,0,0,0);

        transition: box-shadow .15s;
        transition-timing-function: ease-in-out;

        &:hover{
         -webkit-box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
-moz-box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
        }
      }
      /* the parent */
      .slick-list {
        margin: 0;
      }
    }

    .slick-track{
      margin-top: 15px;
      margin-bottom: 15px;
    }
  }
}